
/* eslint-disable @typescript-eslint/no-non-null-assertion */
import { defineComponent, ref, PropType, onBeforeMount } from 'vue'

type Track = {
  color: string
  name: string
  artist: string
  cover: string
  source: string
}

export default defineComponent({
  props: {
    tracks: {
      type: Array as PropType<Array<Track>>,
      required: true,
    },
  },
  setup(props, { emit }) {
    const audio = ref<HTMLAudioElement>()
    const barWidth = ref('0')
    const isTimerPlaying = ref(false)
    const currentTrack = ref<Track>()
    const currentTrackIndex = ref(0)

    const resetPlayer = () => {
      barWidth.value = '0'
      audio.value!.currentTime = 0
      audio.value!.src = currentTrack.value!.source
      emit('on-reset')
      setTimeout(() => {
        if (isTimerPlaying.value) audio.value!.play()
        else audio.value!.pause()
      }, 300)
    }
    const generateTime = () => {
      // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
      const width = (100 / audio.value!.duration) * audio.value!.currentTime
      barWidth.value = width + '%'
    }
    const nextTrack = () => {
      if (currentTrackIndex.value < props.tracks.length - 1)
        currentTrackIndex.value++
      else currentTrackIndex.value = 0

      currentTrack.value = props.tracks[currentTrackIndex.value]
      resetPlayer()
    }
    const prevTrack = () => {
      if (currentTrackIndex.value > 0) currentTrackIndex.value--
      else currentTrackIndex.value = props.tracks.length - 1
      currentTrack.value = props.tracks[currentTrackIndex.value]
      resetPlayer()
    }
    const play = () => {
      audio.value!.volume = 0.1
      if (audio.value?.paused) {
        audio.value?.play()
        isTimerPlaying.value = true
      } else {
        audio.value?.pause()
        isTimerPlaying.value = false
      }
    }
    const updateBar = (x: number) => {
      const progressBar = document.querySelector('.progress') as HTMLDivElement
      const maxDuration = audio.value!.duration
      const position = x - progressBar.offsetLeft
      let percentage = (100 * position) / progressBar.offsetWidth

      if (percentage > 100) percentage = 100
      if (percentage < 0) percentage = 0

      barWidth.value = `${percentage}%`
      audio.value!.currentTime = (maxDuration * percentage) / 100
      audio.value!.play()
    }
    const clickProgress = (e: MouseEvent) => {
      isTimerPlaying.value = true
      audio.value?.pause()
      updateBar(e.pageX)
    }
    const calculate = (value: number) => (value < 10 ? `0${value}` : `${value}`)
    const duration = () => {
      const durmin = Math.floor((audio.value?.duration || 0) / 60)
      const dursec = Math.floor((audio.value?.duration || 0) - durmin * 60)
      return `${calculate(durmin)}:${calculate(dursec)}`
    }
    const currentTime = () => {
      const curmin = Math.floor((audio.value?.currentTime || 0) / 60)
      const cursec = Math.floor((audio.value?.currentTime || 0) - curmin * 60)
      return `${calculate(curmin)}:${calculate(cursec)}`
    }
    onBeforeMount(() => {
      currentTrack.value = props.tracks[0]
      audio.value = new Audio()
      audio.value.src = currentTrack.value.source

      audio.value.ontimeupdate = () => generateTime()
      audio.value.onloadedmetadata = () => generateTime()
      audio.value.onended = () => {
        nextTrack()
        isTimerPlaying.value = true
      }
    })

    return {
      currentTrackIndex,
      isTimerPlaying,
      barWidth,
      play,
      nextTrack,
      prevTrack,
      currentTime,
      duration,
      clickProgress,
    }
  },
})
